export default {
	data() {
		return {
			source: 0,
			listQuery: { //查询分页
				name:"", 
				pageNo: 1,
				pageSize: 10
			},
			addressList: []
		}
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
		this.loadData();
	},
	methods: {
		async loadData(){
			var _this = this;
			//获取数据
			this.sendRequest({
				url : "get_address_list",
				data : this.listQuery,
				method : "get",
				header: {'content-type' : "application/x-www-form-urlencoded"},
				success:function (res) {
					 let dataPage= res.data;
					 let addressList = dataPage.datas;
					 if(_this.listQuery.pageNo==1){
					 	_this.addressList = [];
					 }
					 _this.addressList = _this.addressList.concat(addressList);
				}
			});  
		},
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().orderInfo.addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			// this.addressList.unshift(data);
			this.loadData();
			console.log(data, type);
		}
	}
}
